// Variables
@import "variables";

.circle-shopping-cart{
	background-color: #2196f3;
	border-radius: 50%;
	padding: 0.7em 1em;
}

.products-container{
	.product{
		margin-top: 1em;
		margin-bottom: 1em;
		
	}
}

.product-avatar{
	max-width: 100%;
}

body{
	font-size: 14px;
	background: #F2F4F5;
}

.background{
	position: absolute;
	left: 0;
	top: 0;
	right: 0;
	bottom: 0;
	background:
	50% 50% no-repeat;
	 -webkit-background-size: cover;
     -moz-background-size: cover;
     -o-background-size: cover;
     background-size: cover;
}

.header .background{
	background-image: url("../img/bg.jpg");
	height: 100%;
	z-index: -1;
}

.header-text{
	color: #ffffff;
}

.jumbotron {
 padding:0 !important;
 margin:0;
 color: #777777;
 background: #F2F4F5;
}

.jumbotron h1 {
 color: #fa5950;
 font-size: 27px;
}

.section-padding {
 padding-top: 520px;
}

.grey {
 color:#777777;
}

.showcase-box{
	height: 230px;
	padding: 2px;
}

.showcase-item{
	color: #777777;
	padding: 20px;
	height: 100%;
	width: 100%;
}

.bottom-menu {
background: #e2e2e2;
color: #bcc3ca;
padding: 39px 0 42px;
margin: 150px 0 0 0;
}
.bottom-menu .navbar-brand {
 font-size: 20px;
 padding: 0;
}
.bottom-menu .title {
font-size: 13px;
font-weight: 700;
margin-top: 0;
}
.bottom-menu a {
color: inherit;
}
.active .bottom-menu a, .bottom-menu a:hover .bottom-menu a:focus
{
color: #e47054;
text-decoration: none;
}
.bottom-menu .bottom-links{
font-size: 14px;
line-height: 1.286;
}
.bottom-links{
margin: 0;
padding: 0;
list-style: none;
}
.bottom-links li{
display: block;
float: left;
margin: 0 20px 0 0;
}

.well-pet{
	color: #777777;
	height: 2000px;
	width: 100%;
}

.no-marging{
	margin: 0px;
}

.top_space{
	margin-top: 1em;
}

.no-padding{
	padding: 0px;
}

.large_padding{
	padding: 10px 15px;
}

.big-padding{
	padding: 30px;
}

.blue-grey{
	background-color: #37474f;
}

.white-text{
	color: white;
}

.red-text{
	color:rgb(200,50,50);
}

.inline-block{
	display: inline-block;
}

.table{
	background-color: white;
}

.absolute{
	position: absolute;
}

.navbar{
	margin-bottom: 0px;
}

.white{
 background-color: white;
}

.floating{
	position:fixed;
	bottom: 2em;
	right: 2em;
	z-index: 100;
}

.product{
	width: 600px;
	max-width: 100%;
	padding: 1em;
	position: relative;

	h1{
		padding-bottom: 10px;
		border-bottom: solid 1px;
	}

	.actions{
		right: 1em;
		top: 10px;
	}
}

.sale-data{
	padding: 1em;
	text-align: center;
	border-right: solid 1px #222;
	span{
		display: block;
		font-size: 2.2em;
		color: #3f51b5;
	}
}



/*Status order*/

.creado{
	background-color: #d4e157;
}

.highlight{
	-webkit-animation: highlight 1.5s;
	animation: highlight 1.5s;
}

@-webkit-keyframes highlight{
	50%{
		background-color: #ffff00;
		color: black;
	}
	100%{
		background-color: inherit;
	}
}

@keyframes highlight{
	50%{
		background-color: #ffff00;
		color: black;
	}
	100%{
		background-color: inherit;
	}
}